/**
 * Get the type by string length
 * 
 * @private
 * @param {String} text
 * @param {Number} nCorrectLevel
 * @return {Number} type
 */
function _getTypeNumber(text, nCorrectLevel) {			
	var nType = 1;
	var length = _getUTF8Length(text);

	for (var i = 0, len = QRCodeLimitLength.length; i < len; i++) {
		if(nCorrectLevel === QRErrorCorrectLevel.L)
			if(length <= QRCodeLimitLength[i][0]) break;
		if(nCorrectLevel === QRErrorCorrectLevel.M)
			if(length <= QRCodeLimitLength[i][1]) break;
		if(nCorrectLevel === QRErrorCorrectLevel.Q)
			if(length <= QRCodeLimitLength[i][2]) break;
		if(nCorrectLevel === QRErrorCorrectLevel.H)
			if(length <= QRCodeLimitLength[i][3]) break;

		nType++;
	}

	if(nType > QRCodeLimitLength.length)
		throw new Error("Too long data");

	return nType;
}

function _getUTF8Length(text) {
	var replacedText = encodeURI(text).replace(/\%[0-9a-fA-F]{2}/g, 'a');
	return replacedText.length + (replacedText.length != text ? 3 : 0);
}

class QRCode {
	static CorrectLevel = QRErrorCorrectLevel;

	/**
	 * @param {Object|String} option
	 * @param {String} option.text QRCode link data
	 * @param {Number} [option.width=100%]
	 * @param {Number} [option.height=100%]
	 * @param {String} [option.colorDark="#000000"]
	 * @param {String} [option.colorLight="#ffffff"]
	 * @param {String} [option.correctLevel="H"] [L|M|Q|H] 
	 */
	constructor(option) {
		option ??= {};
		this._opt = {
			width: option.width ?? '100%',
			height: option.height ?? '100%',
			colorDark: option.colorDark ?? "#000000",
			colorLight: option.colorLight ?? "#ffffff",
			correctLevel: QRErrorCorrectLevel[option.correctLevel ?? 'H']
		};
	}

	/**
	 * Make the QRCode
	 * @param {String} text data
	 */
	makeSVG(text) {
		let {width, height, colorLight, colorDark, correctLevel} = this._opt;

		let model = new QRCodeModel(_getTypeNumber(text, correctLevel), correctLevel);
		model.addData(text);
		model.make();

		var nCount = model.getModuleCount();
		let svg = `<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 ${nCount} ${nCount}" width="${width}" height="${height}">
			<rect fill="${colorLight}" width="${nCount}" height="${nCount}"></rect>
		`.replace(/[\t\n\r]/g, '');

		// svg += `<circle fill="${colorDark}" cx="0.5" cy="0.5" r="2.8%" id="t"></circle>`;
		svg += `<rect fill="${colorDark}" width="1" height="1" id="t"></rect>`;

		for (var row = 0; row < nCount; row++) {
			for (var col = 0; col < nCount; col++) {
				if (model.isDark(row, col))
					svg += `<use href="#t" x="${col}" y="${row}"></use>`
			}
		}

		return svg + `</svg>`;
	}
}

